@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Gochi+Hand&family=Permanent+Marker&family=Lobster&family=Architects+Daughter&display=swap);
html,
body,
#root {
    height: 100%;
    width: 100%;
}
body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    color: white;
    background: #123 url(/static/media/dark.3c9d56d1.jpeg) no-repeat fixed left top;
    background-size: 100% 100%;
    
}

@media screen and (max-width: 600px) {
body{
    background: #123 url(/static/media/shiva.9609e48b.jpg) no-repeat fixed;
    background-size: 100% 100%;
	}
}
.game{
  display: grid;
    justify-content: center;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 50px;
  font-weight: bold;
  margin-right: -1px;
  margin-top: -1px;
  text-align: center;
  width: 80px;
  line-height: 80px;
  height: 80px;
  color: #fff;
  font-weight: bold;
  background-color: #123;
  border: 3px solid aqua;   
 }
 
 .O{
  background: blue;
 }
 .X{
  background: green;
 }
 
 .row{
  display: flex;
 }

 .info{
  font: 2em Cursive;
  
 }

body{
	
	
}

.container {
  position: absolute;
  margin-top: 5em;
  left: 15px;
  right: 15px;
}

.container-enter {
  opacity: 0;
  transform: scale(1.5);
}

.container-enter-active {
  opacity: 1;
  transform: scale(1);
   -webkit-transition: opacity 500ms, transform 00ms;
}

.container-exit {
  opacity: 1;
  transform: scale(1);
}

.container-exit-active {
  opacity: 0;
  transform: scale(0.5);
   -webkit-transition: opacity 500ms, transform 500ms;
}



